import {
  Grid,
  InputAdornment,
  TextField,
  Typography,
  useMediaQuery,
  useTheme,
} from "@mui/material";
import { Box } from "@mui/system";
import React, { useMemo, useState } from "react";
import { useSelector } from "react-redux";

const Currencies = ({ t, setCurrency, currency, formik }) => {
  const theme = useTheme();
  const md = useMediaQuery(theme.breakpoints.down(992));

  const [search, setSearch] = useState("");
  const [open, setOpen] = useState(false);

  const { СurrenciesList } = useSelector(
    ({ СurrenciesReducer }) => СurrenciesReducer
  );

  const searchItem = useMemo(
    () =>
      СurrenciesList.filter((item) =>
        item.currency
          .toLocaleLowerCase()
          .startsWith(search.toLocaleLowerCase().trim())
      ),
    // eslint-disable-next-line
    [search]
  );

  const Сurrencies = search ? searchItem : СurrenciesList;

  return (
    <>
      <TextField
        fullWidth
        value={search}
        onChange={(e) => {
          setSearch(e.target.value);
          setOpen(true);
        }}
        variant="outlined"
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <svg
                width="24"
                height="24"
                viewBox="0 0 24 24"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M11.5 21C16.7467 21 21 16.7467 21 11.5C21 6.25329 16.7467 2 11.5 2C6.25329 2 2 6.25329 2 11.5C2 16.7467 6.25329 21 11.5 21Z"
                  stroke="#B8C0CC"
                  stroke-width="1.5"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                />
                <path
                  d="M22 22L20 20"
                  stroke="#B8C0CC"
                  stroke-width="1.5"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                />
              </svg>
            </InputAdornment>
          ),
        }}
        placeholder={t("tickerOrPair")}
      />
      <Box
        sx={
          md
            ? {
                "&::-webkit-scrollbar": { display: "none" },
                height: open ? "100%" : "0%",
                position: open ? "fixed" : "absolute",
                width: "100%",
                top: 0,
                zIndex: 1000,
                backgroundColor: "#000",
                overflow: "scroll",
              }
            : { height: 450, overflow: "scroll" }
        }
      >
        <Box
          mt={2}
          component="table"
          sx={{
            display: "table",
            width: "100%",
            borderSpacing: 0,

            "& thead  td": {
              pb: 1,
            },

            "& thead  p": {
              textAlign: "center",
            },

            "& tbody tr": {
              transition: "all 0.1s linear",
              "&:hover": {
                backgroundColor: "#ffffff50",
              },
            },

            "& tbody td ": {
              cursor: "pointer",

              "& p": {
                p: 1,
              },

              "& img": {
                pl: 1,
              },
            },

            "& tbody td:last-child ": {
              textAlign: "end",
              pr: 1,
            },
          }}
        >
          <thead>
            <td>
              <Typography variant="body2" color="#FFF" fontWeight="500">
                Пары:
              </Typography>
            </td>
            <td>
              <Typography variant="body2" color="#FFF" fontWeight="500">
                Цена:
              </Typography>
            </td>
            <td>
              <Typography variant="body2" color="#FFF" fontWeight="500">
                Изменения:
              </Typography>
            </td>
          </thead>
          <tbody>
            {Array.isArray(Сurrencies) &&
              Сurrencies
                .filter((item) => item.currency !== "USDT")
                .map((item) => (
                  <tr
                    style={{
                      backgroundColor:
                        currency?.currency === item.currency && "#ffffff50",
                    }}
                    onClick={() => {
                      setCurrency(item);
                      formik.setFieldValue("price", item.rate);
                    }}
                  >
                    <td
                      style={{
                        display: "flex",
                        alignItems: "center",
                      }}
                    >
                      <img
                        style={{ objectFit: "contain", marginRight: 8 }}
                        width="28"
                        height="28"
                        src={item.logo}
                        alt=""
                      />
                      <Typography variant="body2" color="#FFF">
                        {item.currency}/USDT
                      </Typography>
                    </td>
                    <td>
                      <Typography variant="body2" color="#FFF">
                        {item.rate}
                      </Typography>
                    </td>
                    <td>
                      <Typography
                        variant="body2"
                        color={
                          item?.difference && item.difference[0] === "+"
                            ? "#10B981!important"
                            : item?.difference && item.difference[0] === "-"
                            ? "#EF4444!important"
                            : "#A0ABBB!important"
                        }
                        ml={
                          (item?.difference && item.difference[0] === "+") ||
                          (item?.difference && item.difference[0] === "-")
                            ? 0
                            : 0.9
                        }
                      >
                        {" "}
                        {item?.difference === undefined
                          ? "0.00"
                          : item?.difference}
                      </Typography>
                    </td>
                  </tr>
                ))}
          </tbody>
        </Box>
      </Box>
    </>
  );
};

export default Currencies;
