import {
  Button,
  InputAdornment,
  Slider,
  Tab,
  Tabs,
  TextField,
  Typography,
} from "@mui/material";
import { Box } from "@mui/system";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import Orders from "../../../components/Orders";
import Inputs from "../../../components/Orders/Inputs";
import { getBuyConfirm } from "../../../redux/reducers/buy";
import { getSellConfirm } from "../../../redux/reducers/sell";

const Fields = ({
  dealType,
  currency,
  t,
  formik,
  balance,
  setOrder,
  order,
  values,
  setResponse,
  setDealType,
}) => {
  const dispatch = useDispatch();

  function valuetext(value) {
    return `${value}%`;
  }

  const { answerCreateBuy, answerBuyConfirm } = useSelector(
    ({ BuyReducer }) => BuyReducer
  );
  const { answerCreateSell, answerSellConfirm } = useSelector(
    ({ SellReducer }) => SellReducer
  );

  useEffect(() => {
    if (dealType === "buy")
      setResponse({
        ...answerCreateBuy,
        open: answerCreateBuy.severity === 200 ? false : true,
      });
    else if (dealType === "sell")
      setResponse({
        ...answerCreateSell,
        open: answerCreateSell.severity === 200 ? false : true,
      });

    if (answerCreateBuy.severity === 200) {
      dispatch(getBuyConfirm(answerCreateBuy?.message?.confirm?.id));
    }

    if (answerCreateSell.severity === 200) {
      dispatch(getSellConfirm(answerCreateSell?.message?.confirm?.id));
    }
    // eslint-disable-next-line
  }, [answerCreateBuy, answerCreateSell]);

  useEffect(() => {
    formik.setFieldValue("amountInUSDT", "");

    // eslint-disable-next-line
  }, [dealType]);

  useEffect(() => {
    if (values.amountInUSDT)
      formik.setFieldValue(
        "total",
        (values.amountInUSDT / values.price)?.toFixed(currency.decimal)
      );
  }, [formik.values.amountInUSDT, formik.values.price]);

  useEffect(() => {
    if (answerSellConfirm) {
      setResponse(
        answerSellConfirm.severity === 200
          ? {
              ...answerSellConfirm,
              message: { messages: t("dealSuccessfullyCreated") },
            }
          : answerSellConfirm
      );
    }
  }, [answerSellConfirm]);

  useEffect(() => {
    if (answerBuyConfirm) {
      setResponse(
        answerBuyConfirm.severity === 200
          ? {
              ...answerBuyConfirm,
              message: { messages: t("dealSuccessfullyCreated") },
            }
          : answerBuyConfirm
      );
    }
  }, [answerBuyConfirm]);

  const handleChange = (type, value) => {
    setDealType(value);
  };

  return (
    <>
      <Tabs
        variant="scrollable"
        sx={{
          mb: 2,
          ".MuiTabs-flexContainer": {
            width: "100%",
            flexDirection: "row!important",
          },

          "& .MuiTabs-indicator": {
            display: "block",
            backgroundColor: dealType === "buy" ? "#FCE34D" : "#FF3B69",
          },

          "& button": {
            width: "50%",
            color: "#FFF",

            "&:first-child": {
              "&.Mui-selected": {
                color: "#FCE34D!important",
              },
            },

            "&:last-child": {
              "&.Mui-selected": {
                color: "#f23646!important",
              },
            },
          },
        }}
        value={dealType}
        onChange={handleChange}
      >
        <Tab value="buy" label={t("buy")} />
        <Tab value="sell" label={t("sell")} />
      </Tabs>
      <Orders t={t} trade setOrder={setOrder} order={order} />
      <Box
        mt={2}
        component="form"
        p="0 16px 0 0"
        onSubmit={formik.handleSubmit}
      >
        <Inputs
          order={order}
          parameter={currency?.currency}
          t={t}
          formik={formik}
          handleValueChange={formik.handleChange}
        />
        <TextField
          type="text"
          fullWidth
          onKeyPress={(event) => {
            if (!/[0-9.]/.test(event.key)) {
              event.preventDefault();
            }
          }}
          variant="outlined"
          name="amountInUSDT"
          value={formik.values.amountInUSDT}
          onChange={formik.handleChange}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">{t("amount")}:</InputAdornment>
            ),
            endAdornment: (
              <InputAdornment position="end">
                {dealType === "sell" ? currency.currency : "USDT"}
              </InputAdornment>
            ),
            inputProps: {
              pattern: "\\d*.\\d*",
            },
          }}
        />
        <TextField
          variant="outlined"
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">{t("total")}:</InputAdornment>
            ),
            endAdornment: (
              <InputAdornment position="end">
                {dealType === "sell" ? "USDT" : currency?.currency}
              </InputAdornment>
            ),
            inputProps: {
              pattern: "\\d*.\\d*",
            },
          }}
          fullWidth
          name="total"
          value={formik.values?.total}
          onChange={formik.handleChange}
        />

        <Slider
          defaultValue={0}
          getAriaValueText={valuetext}
          valueLabelDisplay="auto"
          step={25}
          marks
          sx={{
            "&.MuiSlider-root": {
              color: "#FCE34D!important",
            },
          }}
          onChange={(e) => {
            if (dealType === "sell") {
              formik.setFieldValue(
                "amountInUSDT",
                (currency?.balance * e.target.value) / 100
              );
              formik.setFieldValue(
                "total",
                ((currency?.balance * currency.rate) / 100) * e.target.value
              );
            } else {
              formik.setFieldValue(
                "amountInUSDT",
                (balance * e.target.value) / 100
              );
            }
          }}
          color="primary"
          min={0}
          max={100}
        />

        <Typography color="#777!important" fontSize="14px!important" mb={0.5}>
          {t("availableAmount")}{" "}
          {dealType === "buy" ? balance : currency?.balance}{" "}
          {dealType === "buy" ? "USDT" : currency?.currency}
        </Typography>
        <Typography color="#777!important" fontSize="14px!important" mb={1}>
          {t("MinAmount")}: {currency?.min_qty} {currency?.currency}
        </Typography>

        <Button
          size="large"
          sx={{ color: "#000!important" }}
          fullWidth
          color="primary"
          type="submit"
          variant="contained"
        >
          {t("confirm")}
        </Button>
      </Box>
    </>
  );
};

export default Fields;
