import { IconButton, makeStyles, Modal } from "@material-ui/core";
import React from "react";
import close from "../../../../assets/images/state/close.svg";
const JoinCardImg = ({ open, handleClose, img }) => {
  const classes = useStyles();
  return (
    <Modal
      open={open}
      onClose={handleClose}
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
    >
      <div className={classes.modal}>
        <IconButton
          aria-label="close"
          onClick={() => handleClose(false)}
          className={classes.close}
        >
          <img src={close} alt="" />
        </IconButton>
        <img className={classes.img} src={img} alt="cheque" />
      </div>
    </Modal>
  );
};

export default JoinCardImg;

const useStyles = makeStyles((theme) => ({
  modal: {
    position: "fixed",
    left: 0,
    top: 0,
    zIndex: 999999,
    width: "100vw",
    height: "100vh",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    backgroundColor: "rgba(0, 0, 0, 0.6)",
  },
  close: {
    position: "absolute",
    right: 15,
    top: 15,
  },
  img: {
    maxWidth: "70%",
  },
}));
