import React, { useEffect } from "react";
import { load } from "@2gis/mapgl";
import MapWrapper from "./MapWrapper";

const Map = ({ center, setCenter }) => {
	
  useEffect(() => {
    let map;

    load().then((mapglAPI) => {
      map = new mapglAPI.Map("map-container", {
        center: center,
        zoom: 13,
        key: "7c70a320-711f-4e72-99d6-54bccc526eab",
      });

      const marker = new mapglAPI.Marker(map, {
        coordinates: center,
      });

      marker.on("click", (e) => {
        marker.hide();
      });

       setCenter && map.on("click", (ev) => {
        setCenter(ev.lngLat);
        marker.setCoordinates(ev.lngLat);
        marker.show();
      })

      document
        ?.getElementsByClassName("mapgl_100uyFkiYndX6GvJkrNTro")[0]
        ?.setAttribute("type", "button");

      document
        ?.getElementsByClassName("mapgl_100uyFkiYndX6GvJkrNTro")[1]
        ?.setAttribute("type", "button");
    });
    return () => map && map.destroy();
    // eslint-disable-next-line
  }, []);

  return (
    <div style={{ width: "100%", height: "468px" }}>
      <MapWrapper />
    </div>
  );
};

export default Map;
