import { Button, Tab, Tabs } from "@mui/material";
import { Box } from "@mui/system";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  cancelOrder,
  getOrderList,
  getOrderListExecuted,
} from "../../../redux/reducers/orders";

const Orders = ({ t }) => {
  const dispatch = useDispatch();

  const [state, setState] = useState(false);
  const [orders, setOrders] = useState("order");

  const { order_list, order_list_executed } = useSelector(
    ({ orders }) => orders
  );

  useEffect(() => {
    if (state) {
      setTimeout(() => {
        dispatch(getOrderList());
        dispatch(getOrderListExecuted());
      }, 1000);
      setState(false);
    }
    // eslint-disable-next-line
  }, [state]);

  const handleChangeOrders = (type, value) => {
    setOrders(value);
  };

  return (
    <>
      <Box height="30%" mt={2}>
        <Tabs
          value={orders}
          onChange={handleChangeOrders}
          sx={{
            mb: 2,
            ".MuiTabs-flexContainer": {
              width: "100%",
              flexDirection: "row!important",
            },

            "& .MuiTabs-indicator": {
              display: "block",
              backgroundColor: "#FCE34D",
            },

            "& button": {
              width: "50%",
              color: "#FFF",

              "&.Mui-selected": {
                color: "#FCE34D!important",
              },
            },
          }}
        >
          <Tab value="order" label={t("order")} />
          <Tab value="historyOrder" label={t("historyOrder")} />
        </Tabs>

        {orders === "historyOrder" && (
          <Box
            component="table"
            sx={{
              width: "80%",
              "& thead td": {
                textAlign: "center",
                fontSize: 14,
                fontWeight: "500",
              },

              "& tbody td": {
                textAlign: "center",
                fontWeight: "300",
                fontSize: 12,
              },
            }}
          >
            <thead>
              <tr>
                <td>{t("type")}</td>
                <td>{t("side")}</td>
                <td>{t("pair")}</td>
                <td>{t("Amount")}</td>
                <td>{t("quantity")}</td>
                <td>{t("condition")}</td>
                <td>{t("action")}</td>
              </tr>
            </thead>
            <tbody>
              {order_list_executed?.orders
                // eslint-disable-next-line
                ?.filter((item) => item.status.value != 3)
                .map((item) => (
                  <tr>
                    <td>{item.type}</td>
                    <td>{item.side}</td>
                    <td>{item.currency}/USDT</td>
                    <td>{item.sum}</td>
                    <td>{item.quantity}</td>
                    <td>
                      {item.side === "Buy" || item.side === "Покупка"
                        ? ">="
                        : "<="}
                      &#160;
                      {item.price}
                    </td>
                    <td>
                      <Button
                        size="large"
                        disabled
                        onClick={() => {
                          setState(true);
                          dispatch(cancelOrder(item.id));
                        }}
                        color="primary"
                        variant="contained"
                      >
                        {t("cancel")}
                      </Button>
                    </td>
                  </tr>
                ))}
            </tbody>
          </Box>
        )}

        {orders === "order" && (
          <Box
            component="table"
            sx={{
              width: "80%",
              "& thead td": {
                textAlign: "center",
                fontSize: 14,
                fontWeight: "500",
              },
              "& .MuiButton-root": {
                color: "#000!important",
              },

              "& tbody td": {
                textAlign: "center",
                fontWeight: "300",
                fontSize: 12,
              },
            }}
          >
            <thead>
              <tr>
                <td>{t("type")}</td>
                <td>{t("side")}</td>
                <td>{t("pair")}</td>
                <td>{t("Amount")}</td>
                <td>{t("quantity")}</td>
                <td>{t("condition")}</td>
                <td>{t("action")}</td>
              </tr>
            </thead>
            <tbody>
              {order_list?.orders
                // eslint-disable-next-line
                ?.filter((item) => item.status.value != 3)
                .map((item) => (
                  <tr>
                    <td>{item.type}</td>
                    <td>{item.side}</td>
                    <td>{item.currency}/USDT</td>
                    <td>{item.sum}</td>
                    <td>{item.quantity}</td>
                    <td>
                      {item.side === "Buy" || item.side === "Покупка"
                        ? ">="
                        : "<="}
                      &#160;
                      {item.price}
                    </td>
                    <td>
                      <Button
                        size="large"
                        onClick={() => {
                          setState(true);
                          dispatch(cancelOrder(item.id));
                        }}
                        color="primary"
                        variant="contained"
                      >
                        {t("cancel")}
                      </Button>
                    </td>
                  </tr>
                ))}
            </tbody>
          </Box>
        )}
      </Box>
    </>
  );
};

export default Orders;
