import React, { useState } from "react";
import {
  Dialog,
  DialogContent,
  makeStyles,
  Typography,
  Button,
  CircularProgress,
  Box,
  Snackbar,
} from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router";
import { Form, Formik } from "formik";
import {
  AcceptPaymentGet,
  AcceptPaymentSent,
  getDealById,
  setAcceptPaymentGet,
  setAcceptPaymentSent,
} from "../../../../redux/reducers/P2P";
import { Alert } from "@mui/material";
const JoinCard = ({ t, open, setOpen, user, image, type }) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const { id } = useParams();
  const {
    answerAcceptPaymentSent,
    answerAcceptPaymentGet,
    answerAcceptPayment,
  } = useSelector(({ P2PReducer }) => P2PReducer);
  const [loading, setLoading] = useState(false);

  const fileInput = React.useRef(true);
  const firstUpdate = React.useRef(true);
  React.useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }
    if (answerAcceptPaymentSent.severity === 202) {
      setTimeout(() => {
        setOpen(false);
        dispatch(setAcceptPaymentSent({ ...alert, open: false }));
        dispatch(getDealById(id));
      }, 1300);
    }
    if (answerAcceptPaymentGet.severity === 202) {
      setTimeout(() => {
        setOpen(false);
        dispatch(setAcceptPaymentGet({ ...alert, open: false }));
        dispatch(getDealById(id));
      }, 1300);
    }
    if (answerAcceptPaymentSent.severity || answerAcceptPaymentGet.severity) {
      setLoading(false);
    }
    // eslint-disable-next-line
  }, [answerAcceptPaymentSent.severity, answerAcceptPaymentGet.severity]);

  const owner = type === "card2" && !user;
  const userUSl = type === "card" ? user : owner;
  return (
    <>
      <Snackbar
        open={answerAcceptPayment.open}
        autoHideDuration={2000}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        onClose={() =>
          dispatch(setAcceptPaymentSent({ ...alert, open: false }))
        }
      >
        <Alert
          onClose={() =>
            dispatch(setAcceptPaymentSent({ ...alert, open: false }))
          }
          severity={answerAcceptPayment.severity === 202 ? "success" : "error"}
          variant="filled"
        >
          {answerAcceptPayment.severity === 202
            ? `${t("successfullySent")}`
            : answerAcceptPayment.message}
        </Alert>
      </Snackbar>
      <Dialog
        open={open}
        onClose={() => setOpen(false)}
        scroll="body"
        classes={{ paper: classes.paper }}
      >
        <DialogContent className={classes.DialogContent}>
          <Typography variant="h2" className={classes.title}>
            {t(userUSl ? "paymentСonfirmation" : "p2pСonfirmation")}
          </Typography>
          <Typography
            variant="body1"
            style={{ lineHeight: 1.2, marginBottom: 10 }}
          >
            {t(userUSl ? "warningСheque" : type === 'card2' ? "WarningOwnerSent" : "WarningParticipantSent")}
          </Typography>
          <Formik
            className={classes.form}
            initialValues={{
              image: "",
            }}
            onSubmit={(values) => {
              setTimeout(() => {
                var data = new FormData();
                values.image &&
                  data.append("image", values.image.target.files[0]);
                !userUSl
                  ? dispatch(AcceptPaymentSent(id, data, type))
                  : dispatch(AcceptPaymentGet(id, data, type));
                setLoading(true);
              }, 400);
            }}
          >
            {(formik) => (
              <Form className={classes.form}>
                {image ? (
                  <img
                    src={image}
                    className={classes.VerificationImage}
                    alt="Verification"
                  />
                ) : (
                  !userUSl && (
                    <Box className={classes.input}>
                      <input
                        type="file"
                        name="image"
                        onChange={(e) => formik.setFieldValue("image", e)}
                        id="upload"
                        ref={fileInput}
                        accept="image/*"
                        style={{ display: "none" }}
                      />
                      <label htmlFor="upload">
                        {formik.values.image ? (
                          <img
                            className={classes.VerificationImage}
                            src={window.URL.createObjectURL(
                              formik.values?.image?.target?.files[0]
                            )}
                            alt="IdentityDocument"
                          />
                        ) : (
                          <Button
                            variant="contained"
                            size="large"
                            onClick={() => {
                              fileInput.current.click();
                            }}
                            color="secondary"
                          >
                            {t("SelectFile")}
                          </Button>
                        )}
                      </label>
                    </Box>
                  )
                )}

                <Button
                  type="submit"
                  size="large"
                  className={classes.buttonSubmit}
                  variant="contained"
                  color="primary"
                  disabled={loading}
                >
                  {" "}
                  {loading ? (
                    <CircularProgress className="progress" color="red" />
                  ) : (
                    t(userUSl ? "confirm" : "Send")
                  )}
                </Button>
              </Form>
            )}
          </Formik>
        </DialogContent>
      </Dialog>
    </>
  );
};

export default JoinCard;

const useStyles = makeStyles((theme) => ({
  paper: {
    borderRadius: 16,
    width: "100%",
  },

  input: {
    height: 200,
    width: "100%",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    "& div": {
      height: "100%",
      width: "100%",
    },
  },

  VerificationImage: {
    height: "180px",
    width: "180px",
    objectFit: "cover",
    display: "block",
    margin: "0 auto",
  },

  buttonSubmit: {
    margin: "16px auto 0",
    display: "block",
    width: "100%",
  },
  DialogContent: {
    padding: "40px 31px!important",
    display: "flex",
    justifyContent: "center",
    flexDirection: "column",

    [theme.breakpoints.down(992)]: {
      padding: "16px",
    },
  },

  close: {
    position: "absolute",
    top: 0,
    right: 0,
  },

  title: {
    textAlign: "center",
    color: "#000",
    fontWeight: 500,
    letterSpacing: "-0.04em",
    lineHeight: "56px",
    marginBottom: 24,

    [theme.breakpoints.down(992)]: {
      fontSize: 48,
    },

    [theme.breakpoints.down(768)]: {
      fontSize: 34,
      lineHeight: "40px",
    },
  },

  subtitle: {
    color: "#64748B",
    marginLeft: 10,
  },
}));
