import { makeStyles, Typography, Box } from "@material-ui/core";
import React from "react";
import { Link } from "react-router-dom";

const CryptoBlock = ({ href, item, commission }) => {
  const classes = useStyles();

  return (
    <Link to={href} className={classes.whiteBox}>
      <Box className={classes.box}>
        <Box sx={{ display: "flex", overflow: "hidden", maxWidth: "90%" }}>
          <img src={item.logo} className={classes.img} alt="coin" />
          <Box sx={{ overflow: "hidden", maxWidth: "100%" }}>
            <Typography className={classes.prefix}>{item.name}</Typography>
            <Typography className={classes.name}>{item.currency}</Typography>
          </Box>
        </Box>
      </Box>
      <Box className={classes.bottomBlock}>
        <Typography className={classes.ReplenishBoxCommission}>
          {commission
            ? item.commission === null
              ? "0.00 %"
              : `${item.commission} % `
            : item.balance}
        </Typography>
      </Box>
    </Link>
  );
};

export default CryptoBlock;

const useStyles = makeStyles((theme) => ({
  whiteBox: {
    display: "flex",
    justifyContent: "space-between",
    flexDirection: "column",
    color: "#000",
    background: "#FFFFFF",
    boxShadow: "0px 1px 0px #E2E8F0",
    borderRadius: 8,
    height: "167px",
    transition: "all 0.2s linear",

    "&:hover": {
      boxShadow: "0px 0px 6px 2px #E2E8F0",
    },
  },

  box: {
    display: "flex",
    height: "100%",
    flexDirection: "column",
    alignItems: "flex-start",
    padding: "0 16px",
    justifyContent: "center",
  },
  img: {
    height: 64,
    marginRight: 16,
  },

  prefix: {
    fontSize: 33,
    fontWeight: 500,
    marginBottom: 4,
    whiteSpace: "nowrap",
    textOverflow: "ellipsis",
    overflow: "hidden",
  },

  name: {
    fontSize: 16,
    color: "#64748B",
    fontWeight: 300,
    whiteSpace: "nowrap",
    textOverflow: "ellipsis",
    overflow: "hidden",
  },

  bottomBlock: {
    backgroundColor: "#0D0F11",
    borderRadius: "0 0 8px 8px",
    color: "#fff",
    textAlign: "center",
    padding: "7px 7px 6px 7px",
  },
}));
