import React, { useEffect } from "react";
import { Box, Typography, Button, makeStyles } from "@material-ui/core";
import tether from "./../../assets/images/crypto/tetherIcon.webp";
import { useHistory } from "react-router";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { setBalanceGlobal } from "../../redux/reducers/Main";

const Balance = ({ main }) => {
  const history = useHistory();
  const classes = useStyles();
  const dispatch = useDispatch();

  const { t } = useTranslation();

  const { balance } = useSelector(({ MainReducer }) => MainReducer);
  const { allBalances } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );

  useEffect(() => {
    const usdt = allBalances?.find((item) => item.name === "USDT");
    dispatch(setBalanceGlobal(usdt?.balance));
    // eslint - disable - next - line;
  }, [allBalances]);

  return (
    <Box
      display="flex"
      justifyContent="space-between"
      alignItems="center"
      className={classes.box}
    >
      <Box>
        <Typography
          variant="h4"
          className={classes.title}
          style={{ color: "#323A46", fontWeight: 500 }}
        >
          {t("YourBalance")}
        </Typography>
        <Typography
          variant="h2"
          style={{
            color: "#000",
            fontWeight: 500,
            display: "flex",
            alignItems: "center",
          }}
        >
          <img style={{ width: 51 }} src={tether} alt="tether" />
          &#160;
          {balance}
        </Typography>
      </Box>
      <Box className={classes.buttonBox}>
        <Button
          variant="contained"
          onClick={() => history.push("/dashboard/payment-for-services/106")}
          style={{ width: "100%" }}
          size="large"
          color="primary"
        >
          {t("WithdrawFunds")}
        </Button>
        <div style={{ marginTop: 8 }}>
          <Button
            variant="contained"
            style={{ width: "100%" }}
            onClick={() => history.push("/dashboard/replenish")}
            size="large"
            color="secondary"
          >
            {t("TopUpBalance")}
          </Button>
        </div>
      </Box>
    </Box>
  );
};

export default Balance;

const useStyles = makeStyles((theme) => ({
  title: {
    [theme.breakpoints.down(992)]: {
      fontSize: 48,
    },

    [theme.breakpoints.down(992)]: {
      fontSize: 34,
      lineHeight: "40px",
      marginBottom: 8,
    },
  },

  buttonBox: {
    [theme.breakpoints.down(576)]: {
      width: "100%",
      marginTop: 16,
    },
  },

  box: {
    borderBottom: "2px solid #E7EAEE",
    paddingBottom: 40,
    marginBottom: 40,

    [theme.breakpoints.down(992)]: {
      paddingBottom: 36,
      marginBottom: 24,
    },

    [theme.breakpoints.down(576)]: {
      flexDirection: "column",
      paddingBottom: 16,
      marginBottom: 16,
    },

    "& h1": {
      [theme.breakpoints.down(576)]: {
        fontSize: 60,
        lineHeight: "72px",
        marginBottom: 6,
      },
    },
  },
}));
