import React, { useEffect, useLayoutEffect, useState } from "react";
import {
  Container,
  makeStyles,
  Typography,
  Box,
  Button,
  Snackbar,
  TextareaAutosize,
} from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import Balance from "../../../../components/Balance";
import { useHistory, useParams } from "react-router";
import {
  getDealById,
  joinToDeal,
  setAccept,
} from "../../../../redux/reducers/P2P";
import Map from "../../../../components/map/Map";
import { Alert } from "@mui/material";
import JoinCard from "./joinDialog";
import JoinFiatCard from "./joinCard";
import JoinCardImg from "./joinCardImg";
import JoinCash from "./joinCash";
import ConfirmCash from "./confirmCash";
import { getСurrenciesList } from "../../../../redux/reducers/Сurrencies";
const JoinDeals = ({ t }) => {
  const dispatch = useDispatch();
  const history = useHistory();
  const { id } = useParams();
  const classes = useStyles();
  // get data
  const { dealById, answerAccept } = useSelector(
    ({ P2PReducer }) => P2PReducer
  );
  const { profileData } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );

  useEffect(() => {
    dispatch(getDealById(id));
    // eslint-disable-next-line
  }, []);

  const join = () => {
    dispatch(joinToDeal(id));
  };
  const [AcceptCriptToCashModal, setAcceptCriptToCashModal] = useState(false);
  const AcceptCriptToCash = (id) => {
    setAcceptCriptToCashModal(true);
  };

  const firstUpdate = React.useRef(true);
  const [cardAccept, setCardAccept] = useState(false);
  const [fiatCardAccept, setFiatCardAccept] = useState(false);
  const [modal, setModal] = useState(false);
  const [modalCash, setModalCash] = useState(false);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }
    if (dealById?.status === "finished") {
      setTimeout(() => {
        history.push("/dashboard/history");
        dispatch(setAccept({ ...alert, open: false }));
      }, 500);
    }
    if (answerAccept.severity === 202) {
      dispatch(getDealById(id));
      // dispatch(getСurrenciesList());

    }
    // eslint-disable-next-line
  }, [answerAccept?.severity, dealById?.status]);

  const user =
    dealById?.owner === profileData.data?.profile?.email
      ? dealById?.owner
      : dealById?.participant;

  const confirmUser =
    user === dealById?.owner && dealById?.owner_confirm;
  const AcceptPaymentGetUser = user === dealById?.owner;

  const expectations =
    dealById?.type === "card" &&
    dealById?.image &&
    user === dealById?.participant;

  const dealType =
    dealById?.type === "cash" || dealById?.type === "card"
      ? AcceptPaymentGetUser
      : !AcceptPaymentGetUser;
  const dealTypeCash =
    dealById?.type === "cash" || dealById?.type === "card"
      ? !AcceptPaymentGetUser
      : AcceptPaymentGetUser;

  return (
    <section style={{ color: "#000" }}>
      <Container>
        <Snackbar
          open={answerAccept.open}
          autoHideDuration={2000}
          anchorOrigin={{ vertical: "top", horizontal: "center" }}
          onClose={() => dispatch(setAccept({ ...alert, open: false }))}
        >
          <Alert
            onClose={() => dispatch(setAccept({ ...alert, open: false }))}
            severity={answerAccept.severity === 202 ? "success" : "error"}
            variant="filled"
          >
            {answerAccept.message}
          </Alert>
        </Snackbar>

        <Balance />
        <Typography className={classes.title}>{t("deal")}</Typography>
        <Box
          className={classes.wrapper}
          sx={{
            padding: {
              xs: "16px",
              sm: "18px 32px",
              md: "32px 80px",
            },
          }}
        >
          <Box mt="16px" style={{ borderBottom: "2px solid #E2E8F0" }}>
            <Typography
              variant="h5"
              className={classes.supTitle}
              style={{ marginBottom: "20px" }}
            >
              {t("CreaterDeal")}
            </Typography>
            <Box mb={"27px"} sx={{ display: "flex", alignItems: "center" }}>
              <img
                src={dealById.profile?.photo}
                style={{
                  marginRight: 16,
                  width: 80,
                  height: 80,
                  objectFit: "cover",
                  borderRadius: 50,
                }}
                alt="avatar"
              />
              <div>
                <Typography variant="h3" className={classes.name}>
                  {dealById.profile?.first_name} {dealById.profile?.last_name}{" "}
                </Typography>
                <Typography
                  style={{
                    color:
                      dealById.profile?.verify === "Не верифицирован"
                        ? "red"
                        : "#000",
                  }}
                >
                  {dealById.profile?.verify}
                </Typography>
              </div>
            </Box>
          </Box>
          <Box className={classes.box}>
            <Box className={classes.item}>
              <Typography variant="h6" className={classes.supTitle}>
                {t(
                  (
                    dealById?.type === "cash" ||
                    dealById?.type === "card"
                      ? AcceptPaymentGetUser
                      : dealTypeCash
                  )
                    ? "GiveBack"
                    : "GetIt"
                )}
              </Typography>
              <Typography variant="h5" className={classes.text}>
                {dealById?.type === "cash" ||
                dealById?.type === "card"
                  ? dealType
                    ? dealById?.sell_quantity
                    : dealById?.sell_quantity_with_commission
                  : dealType
                  ? dealById?.sell_quantity
                  : dealById?.sell_quantity}{" "}
                {dealById?.sell_currency}
              </Typography>
            </Box>
            <Box className={classes.item}>
              <Typography variant="h6" className={classes.supTitle}>
                {t(
                  (
                    dealById?.type === "cash" ||
                    dealById?.type === "card"
                      ? AcceptPaymentGetUser
                      : dealTypeCash
                  )
                    ? "GetIt"
                    : "GiveBack"
                )}
              </Typography>
              <Typography variant="h5" className={classes.text}>
                {dealById?.type === "cash" ||
                dealById?.type === "card"
                  ? dealType
                    ? dealById?.buy_quantity
                    : dealById?.buy_quantity
                  : dealType
                  ? dealById?.buy_quantity
                  : dealById?.buy_quantity_with_commission}{" "}
                {dealById?.buy_currency}
              </Typography>
            </Box>
            <Box className={classes.item}>
              <Typography USDTography variant="h6" className={classes.supTitle}>
                {t("CreaterDeal")}
              </Typography>
              <Typography variant="h5" className={classes.text}>
                {dealById?.owner ? dealById?.owner : "-----------"}
              </Typography>
            </Box>
            {dealById?.bank_card && (
              <Box className={classes.item}>
                <Typography
                  USDTography
                  variant="h6"
                  className={classes.supTitle}
                >
                  {t("bankCard")}
                </Typography>
                <Typography
                  variant="h5"
                  className={classes.text}
                  style={{ width: 230 }}
                >
                  {dealById?.bank_card.replace(
                    /(\d)(?=(\d{4})+(\D|$))/g,
                    "$1 "
                  )}
                </Typography>
              </Box>
            )}
            {dealById?.name_bank_card && (
              <Box className={classes.item}>
                <Typography
                  USDTography
                  variant="h6"
                  className={classes.supTitle}
                >
                  {t("bankName")}
                </Typography>
                <Typography
                  variant="h5"
                  className={classes.text}
                  style={{ width: 230 }}
                >
                  {dealById?.name_bank_card}
                </Typography>
              </Box>
            )}

            {dealById?.image && (
              <Box className={classes.item}>
                <Typography
                  USDTography
                  variant="h6"
                  className={classes.supTitle}
                >
                  {t("СhequepaymentByCard")}
                </Typography>
                <Button onClick={() => setModal(true)}>
                  <img
                    className={classes.receiptImg}
                    src={dealById?.image}
                    alt="receipt"
                  />
                </Button>
              </Box>
            )}
            <JoinCardImg
              img={dealById?.image}
              open={modal}
              handleClose={setModal}
            />

            {dealById?.phone && (
              <Box className={classes.item}>
                <Typography
                  USDTography
                  variant="h6"
                  className={classes.supTitle}
                >
                  {t("phoneNumber")}
                </Typography>
                <Typography variant="h5" className={classes.text}>
                  {dealById?.phone}
                </Typography>
              </Box>
            )}
            {dealById?.participant_name && (
              <Box className={classes.item}>
                <Typography
                  USDTography
                  variant="h6"
                  className={classes.supTitle}
                >
                  {t("participantName")}
                </Typography>
                <Typography
                  variant="h5"
                  className={classes.text}
                  style={{ width: 230 }}
                >
                  {dealById?.participant_name}
                </Typography>
              </Box>
            )}
            {dealById?.participant_phone && (
              <Box className={classes.item}>
                <Typography
                  USDTography
                  variant="h6"
                  className={classes.supTitle}
                >
                  {t("participantPhone")}
                </Typography>
                <Typography
                  variant="h5"
                  className={classes.text}
                  style={{ width: 230 }}
                >
                  {dealById?.participant_phone}
                </Typography>
              </Box>
            )}
          </Box>

          <Box mt="16px">
            <Typography
              variant="h5"
              className={classes.supTitle}
              style={{ marginBottom: "20px" }}
            >
              {t("Commentary")}
            </Typography>
            {dealById?.description === "" ? (
              <Typography variant="body2">-----------</Typography>
            ) : (
              <TextareaAutosize
                placeholder={t("WriteComment")}
                name="description"
                value={dealById?.description}
                className={classes.textarea}
                disabled={true}
              />
            )}
          </Box>
        </Box>
        <Box style={{ marginTop: 30 }}>
          {(dealById?.type === "cash" ||
            dealById?.type === "cash2") && (
            <Map center={[dealById?.latitude, dealById?.longitude]} />
          )}
        </Box>
        <JoinCard
          t={t}
          open={cardAccept}
          setOpen={setCardAccept}
          user={AcceptPaymentGetUser}
          image={dealById?.image}
          type={dealById?.type}
        />
        <JoinFiatCard
          t={t}
          open={fiatCardAccept}
          setOpen={setFiatCardAccept}
          user={AcceptPaymentGetUser}
        />
        <JoinCash t={t} open={modalCash} setOpen={setModalCash} />

        <Box style={{ marginTop: 30 }}>
            {(dealById?.type === 'card' || dealById?.type === 'card2') && dealById?.status === "process" && (!dealTypeCash ?  dealById[dealById?.type === 'card' ? 'participant_sent' : 'owner_sent'] : !dealById[dealById?.type === 'card' ? 'participant_sent' : 'owner_sent']) &&  <Typography
                variant="h5"
                style={{
                  marginBottom: 20,
                  textAlign: "center",
                  fontWeight: "400",
                  lineHeight: 1,
                }}
              >{t(!dealTypeCash ? "warningСheque" : dealById?.type === 'card2' ? `К Вам успешно присоединился к сделке ${dealById?.participant}
                 , переведите /пополните банковскую карту. Сохраните фото чека/ скриншот перевода и нажмите кнопку отправить.` : "WarningParticipantSent")}</Typography>}
            {(dealById?.type === 'card' || dealById?.type === 'card2') && dealById?.status === "process" && (dealTypeCash ? dealById[dealById?.type === 'card' ? 'participant_sent' : 'owner_sent'] : !dealById[dealById?.type === 'card' ? 'participant_sent' : 'owner_sent']) &&  <Typography
                variant="h5"
                style={{
                  marginBottom: 20,
                  textAlign: "center",
                  fontWeight: "400",
                  lineHeight: 1,
                }}
              >{dealTypeCash && "Отлично, теперь ожидайте подтверждение получения средств на карту от второго участника, после того как он подтвердит , средства будут переведены на Ваш аккаунт"}</Typography>}

              
            {(dealById?.type === 'card2') && dealById?.status === "process" && (dealTypeCash ? !dealById[dealById?.type === 'card' ? 'participant_sent' : 'owner_sent'] : !dealById[dealById?.type === 'card' ? 'participant_sent' : 'owner_sent']) &&  <Typography
                variant="h5"
                style={{
                  marginBottom: 20,
                  textAlign: "center",
                  fontWeight: "400",
                  lineHeight: 1,
                }}
              >{!dealTypeCash && "Вы успешно присоединились к сделке, ожидайте перевода средств на Вашу карту, и следуйте дальнейшем инструкциям." }</Typography>}
            {(dealById?.type === 'card') && dealById?.status === "process" && (dealTypeCash ? !dealById[dealById?.type === 'card' ? 'participant_sent' : 'owner_sent'] : !dealById[dealById?.type === 'card' ? 'participant_sent' : 'owner_sent']) &&  <Typography
                variant="h5"
                style={{
                  marginBottom: 20,
                  textAlign: "center",
                  fontWeight: "400",
                  lineHeight: 1,
                }}
              >{!dealTypeCash &&  `К вам успешно присоединился пользователь ${dealById?.participant} , ожидайте перевода средств на Вашу карту и следуйте дальнейшем инструкциям.` }</Typography>}

          {dealById?.status === "process" &&
            AcceptPaymentGetUser &&
            dealById?.type === "cash" && (
              <Typography
                variant="h5"
                style={{
                  marginBottom: 20,
                  textAlign: "center",
                  fontWeight: "400",
                  lineHeight: 1,
                }}
              >
                К вам присоединился{" "}
                <span style={{ fontWeight: 500 }}>
                  {dealById?.participant_name}(
                  {dealById?.participant_phone})
                </span>
                ,свяжитесь с ним, организуйте встречу, получите наличные
                средства и только после этого, нажимайте кнопку (Подтвердить),
                средства будут переведены на его аккаунт без возвратно
              </Typography>
            )}
          {dealById?.status === "process" &&
            !AcceptPaymentGetUser &&
            dealById?.type === "cash" && (
              <Typography
                variant="h5"
                style={{
                  marginBottom: 20,
                  textAlign: "center",
                  fontWeight: "400",
                  lineHeight: 1,
                }}
              >
                Вы успешно присоединились к {dealById?.owner}(
                {dealById?.phone}), свяжитесь с ним, организуйте
                встречу, передайте наличные средства и убедитесь, чтобы второй
                участник нажал кнопку «Подтвердить», после этого средства будут
                переведены на ваш аккаунт
              </Typography>
            )}

          {dealById?.status === "process" &&
            !AcceptPaymentGetUser &&
            dealById?.type === "cash2" && (
              <Typography
                variant="h5"
                style={{
                  marginBottom: 20,
                  textAlign: "center",
                  fontWeight: "400",
                  lineHeight: 1,
                }}
              >
              Вы успешно присоединились к {dealById?.owner}(
                {dealById?.phone}), свяжитесь с ним, организуйте встречу, получите наличные средства и только после этого, нажимайте кнопку (Подтвердить), средства будут переведены на его аккаунт без возвратно
              </Typography>
            )}
          {dealById?.status === "process" &&
            AcceptPaymentGetUser &&
            dealById?.type === "cash2" && (
              <Typography
              variant="h5"
              style={{
                marginBottom: 20,
                textAlign: "center",
                fontWeight: "400",
                lineHeight: 1,
              }}
            >
              К вам присоединился{" "}
              <span style={{ fontWeight: 500 }}>
                {dealById?.participant_name}(
                {dealById?.participant_phone})
              </span>
              ,свяжитесь с ним, организуйте
                встречу, передайте наличные средства и убедитесь, чтобы второй
                участник нажал кнопку «Подтвердить», после этого средства будут
                переведены на ваш аккаунт
            </Typography>
            )}

          {dealById?.type === "cash" || dealById?.type === "cash2" ? (
            <Button
              variant="contained"
              color="primary"
              size="large"
          
              className={classes.button}
              onClick={() =>
                dealById?.status === "expectation"
                  ? setModalCash(true)
                  : dealById?.status === "process" && AcceptCriptToCash()
              }
            >
              {t(
                dealById?.status === "expectation"
                  ? "Join"
                  : dealById?.status === "process"
                  ? "confirm"
                  : dealById?.status === "finished" && "Completed"
              )}
            </Button>
          ) : (
            <Button
              variant="contained"
              color="primary"
              size="large"
              className={classes.button}
              disabled={
                expectations ||
                dealById?.status === "finished" ||
                (AcceptPaymentGetUser &&
                  dealById?.status === "expectation") ||
                (AcceptPaymentGetUser && dealById?.owner_sent) ||
                dealById?.participant_sent ||
                (dealById?.type === "card" &&
                  AcceptPaymentGetUser &&
                  !dealById?.participant_sent) ||
                (dealById?.type === "card2" &&
                  !AcceptPaymentGetUser &&
                  dealById?.status !== "expectation" &&
                  !dealById?.owner_sent)
              }
              onClick={() =>
                dealById?.status === "expectation"
                  ? dealById?.bank_card
                    ? join()
                    : setFiatCardAccept(true)
                  : dealById?.status === "process" && !confirmUser
                  ? setCardAccept(true)
                  : null
              }
            >
              {t(
                dealById?.status === "expectation"
                  ? "Join"
                  : dealById?.status === "process" && !confirmUser
                  ? "confirm"
                  : "Completed"
              )}
            </Button>
          )}
        </Box>
      
        <ConfirmCash
          t={t}
          open={AcceptCriptToCashModal}
          setOpen={setAcceptCriptToCashModal}
          id={id}
          type={dealById?.type}
        />
      </Container>
    </section>
  );
};

export default JoinDeals;

const useStyles = makeStyles((theme) => ({
  title: {
    fontSize: "23px",
    fontWeight: 600,
    color: "#000",
    [theme.breakpoints.down("md")]: {
      paddingBottom: "16px",
      fontSize: 19,
    },
  },
  wrapper: {
    background: "#fff",
    boxShadow: "0px 1px 0px #E2E8F0",
    borderRadius: " 12px",
    marginTop: 40,
  },

  button: {
    display: "block",
    margin: "0 auto",
    [theme.breakpoints.down("992")]: {
      width: "100%",
    },
  },

  box: {
    display: "flex",
    justifyContent: "space-between",
    borderBottom: "2px solid #E7EAEE",
    padding: "32px 0",
    textAlign: "center",
    flexWrap: "wrap",
    [theme.breakpoints.down("md")]: {
      padding: "18px 0",
      flexDirection: "column",
    },
  },
  item: {
    width: "25%",
    marginBottom: 20,
    [theme.breakpoints.down("md")]: {
      width: "100%",
      display: "flex",
      justifyContent: "space-between",
      marginBottom: 0,
    },
  },
  supTitle: {
    color: "#64748B",
    fontWeight: 300,
    overflow: "hidden",
    textOverflow: "ellipsis",
    whiteSpace: "nowrap",
    [theme.breakpoints.down("md")]: {
      textAlign: "left",
    },

    [theme.breakpoints.down(576)]: {
      maxWidth: "50%",
    },
  },

  text: {
    fontSize: 23,
    color: "#000",
    fontWeight: 600,
    overflow: "hidden",
    textOverflow: "ellipsis",
    whiteSpace: "nowrap",
    [theme.breakpoints.down("md")]: {
      fontSize: 19,

      textAlign: "right",
    },

    [theme.breakpoints.down(576)]: {
      maxWidth: "50%",
    },
  },
  img: {
    width: 140,
    height: 40,
    objectFit: "contain",
    [theme.breakpoints.down("md")]: {
      width: 105,
      height: 23,
    },
  },
  receiptImg: {
    width: 200,
    height: 100,
    objectFit: "contain",
    [theme.breakpoints.down("md")]: {
      width: 155,
      height: 70,
    },
  },
  textarea: {
    padding: "16px 24px",
    outline: "none",
    width: "100%",
    height: "180px!important",
    background: "#FFFFFF",
    marginTop: 8,
    resize: "none",
    boxShadow: "0px 1px 0px #E2E8F0",
    borderRadius: "8px",
    fontSize: 20,
    transition: "all 0.2s linear",
    border: "2px solid #E7EAEE",
    "&:focus": {
      border: "2px solid #FCE34D",
    },
  },
}));
