import React, { useEffect, useState } from "react";
import { Grid } from "@mui/material";
import { Box } from "@mui/system";
import { useDispatch, useSelector } from "react-redux";
import Chart from "../Buy/Chart";
import { useFormik } from "formik";
import {
  createOrder,
  getOrderList,
  getOrderListExecuted,
} from "../../../redux/reducers/orders";
import { CreateBuy } from "../../../redux/reducers/buy";
import { CreateSell } from "../../../redux/reducers/sell";
import SnackBar from "../../../components/SnackBar";
import Currencies from "./Currencies";
import Fields from "./Fields";
import Orders from "./Orders";
import { OrderAlert } from "../../../components/Orders";
import { useRef } from "react";

const Trade = ({ t }) => {
  const dispatch = useDispatch();

  const { balance } = useSelector(({ MainReducer }) => MainReducer);
  const { СurrenciesList } = useSelector(
    ({ СurrenciesReducer }) => СurrenciesReducer
  );
  const { order_data } = useSelector(({ orders }) => orders);

  const [order, setOrder] = useState("market");
  const [dealType, setDealType] = useState("buy");
  const [response, setResponse] = useState({});
  const [currency, setCurrency] = useState({ currency: "BTC" });
  const ws = useRef();

  const formik = useFormik({
    initialValues: {
      amountInUSDT: "",
      total: "",
      price: "",
      stopPrice: "",
    },
    onSubmit: (values) => {
      if (order !== "market") {
        if (order === "limit") {
          const data = {
            order_type: order,
            order_side: dealType,
            currency: currency?.currency,
            price: values.price,
            amount: values.amountInUSDT,
            quantity: values.total,
          };
          dispatch(createOrder(data));
        } else {
          const data = {
            order_type: order,
            order_side: dealType,
            currency: currency?.currency,
            price: values.price,
            amount: values.amountInUSDT,
            quantity: values.total,
            stop_price: values.stopPrice,
          };
          dispatch(createOrder(data));
        }
      } else {
        const data = { sum: values.amountInUSDT, currency: currency?.currency };
        if (dealType === "buy") dispatch(CreateBuy(data));
        else dispatch(CreateSell(data));
      }
    },
  });

  const values = formik.values;

  useEffect(() => {
    ws.current = new WebSocket("ws://ns1.netex.kg/ws/currency/", [
      "Bearer",
      "UXJZdWxwVGVYZVpWaldNZFV4d1REZ0R1RnBvYjlVTTVvR1ZZenVRMk5LSWVTNFZoQk5hbHM1RzQ3MVEzbUtFaw==",
    ]);
    const request = new Date().getTime();

    // ws.onopen = function () {
    //   ws.send(
    //     JSON.stringify({
    //       action: "list",
    //       request_id: request,
    //     })
    //   );

    //   ws.send(
    //     JSON.stringify({
    //       action: "subscribe_to_activity",
    //       request_id: request,
    //     })
    //   );
    // };
    // ws.onclose = function (e) {
    //   console.error("Chat socket closed unexpectedly");
    // };
  }, []);

  useEffect(() => {
    dispatch(getOrderList());
    dispatch(getOrderListExecuted());

    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    if (Array.isArray(СurrenciesList)) {
      const btc = СurrenciesList.find((item) => item.currency === "BTC");

      formik.setFieldValue("price", btc?.rate);
      setCurrency(btc);
    }
  }, [СurrenciesList]);

  useEffect(() => {
    // setLoading(false);

    if (order_data.severity === 200) dispatch(getOrderList());
    // eslint-disable-next-line
  }, [order_data]);

  return (
    <>
      <SnackBar state={response} setState={setResponse} />

      <OrderAlert />

      <Box
        component="section"
        backgroundColor="#161a25"
        color="#FFF"
        sx={{
          pt: { xs: 0, lg: 1 },
          minHeight: "calc(100vh - 57px)",
          overflowY: "hidden",

          "& *": {
            color: "inherit!important",
          },

          "& input": {
            color: "#FFF",
          },

          "& .MuiTextField-root": {
            mb: 1,
          },

          "& .MuiOutlinedInput-input": {
            boxShadow: "none",
          },

          "& fieldset": {
            borderColor: "#FCE34D!important",
          },
        }}
      >
        <Grid container height="70%" sx={{ position: "relative" }} spacing={1}>
          <Grid item xs={12} md={3}>
            <Currencies
              currency={currency}
              t={t}
              setCurrency={setCurrency}
              formik={formik}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <Chart coin={currency} />
          </Grid>
          <Grid item xs={12} md={3}>
            <Fields
              currency={currency}
              setDealType={setDealType}
              dealType={dealType}
              values={values}
              setResponse={setResponse}
              t={t}
              formik={formik}
              balance={balance}
              setOrder={setOrder}
              order={order}
            />
          </Grid>
        </Grid>
        <Orders t={t} />
      </Box>
    </>
  );
};

export default Trade;
