import React, { useState } from "react";
import {
  Dialog,
  DialogContent,
  makeStyles,
  Typography,
  Button,
  CircularProgress,
  TextField,
} from "@material-ui/core";

import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router";
import { Form, Formik } from "formik";
import {
  getDealById,
  joinToDeal,
  setAcceptPaymentGet,
  setAcceptPaymentSent,
} from "../../../../redux/reducers/P2P";
import { CardMaskCustom } from "../../../../components/inputMask/cardMask";

const JoinFiatCard = ({ t, open, setOpen, user }) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const { id } = useParams();
  const { answerAccept } = useSelector(
    ({ P2PReducer }) => P2PReducer
  );
  const [loading, setLoading] = useState(false);

  const firstUpdate = React.useRef(true);

  React.useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }
    if (answerAccept.severity === 202) {
      setTimeout(() => {
        setOpen(false);
        dispatch(setAcceptPaymentSent({ ...alert, open: false }));
        dispatch(getDealById(id));
      }, 1300);
    }
    if (answerAccept.severity === 202) {
      setTimeout(() => {
        setOpen(false);
        dispatch(setAcceptPaymentGet({ ...alert, open: false }));
        dispatch(getDealById(id));
      }, 1300);
    }
    if (answerAccept.severity || answerAccept.severity) {
      setLoading(false);
    }
    // eslint-disable-next-line
  }, [answerAccept.severity, answerAccept.severity]);


  return (
    <>
      <Dialog
        open={open}
        onClose={() => setOpen(false)}
        scroll="body"
        classes={{ paper: classes.paper }}
      >
        <DialogContent className={classes.DialogContent}>
          <Typography variant="h2" className={classes.title}>
            {t("ReceiptWallet")}
          </Typography>
          <Formik
            className={classes.form}
            initialValues={{
              bank_card: "",
              name_bank_card: "",
            }}
            onSubmit={(values) => {
              setTimeout(() => {
              (values.bank_card = values.bank_card.replace(/\s/g, ""))
                dispatch(joinToDeal(id, values));
                setLoading(true);
              }, 400);
            }}
          >
            {({ handleChange, values }) => (
              <Form className={classes.form}>
                <TextField
                  label={t("bankCardNumber")}
                  name="bank_card"
                  value={values.bank_card}
                  onChange={handleChange}
                  variant="outlined"
                  id="formatted-numberformat-input"
                  className={classes.input}
                  InputProps={{
                    inputComponent: CardMaskCustom,
                  }}
                  required
                />
              <div className="label-lg">{t("bankNameLabel")}</div>
              <TextField
                label={t("bankName")}
                value={values.name_bank_card}
                onChange={handleChange}
                variant="outlined"
                name="name_bank_card"
                id="formatted-numberformat-input"
                className={classes.input}
                required
              />
                <Button
                  type="submit"
                  size="large"
                  className={classes.buttonSubmit}
                  variant="contained"
                  color="primary"
                  disabled={loading}
                >
                  {loading ? (
                    <CircularProgress className="progress" color="red" />
                  ) : (
                    t("Send")
                  )}
                </Button>
              </Form>
            )}
          </Formik>
        </DialogContent>
      </Dialog>
    </>
  );
};

export default JoinFiatCard;

const useStyles = makeStyles((theme) => ({
  paper: {
    borderRadius: 16,
    width: "100%",
  },
  VerificationImage: {
    height: "180px",
    width: "180px",
    objectFit: "cover",
    display: "block",
    margin: "0 auto",
  },
  buttonSubmit: {
    margin: "16px auto 0",
    display: "block",
    width: "100%",
  },
  DialogContent: {
    padding: "40px 31px!important",
    display: "flex",
    justifyContent: "center",
    flexDirection: "column",

    [theme.breakpoints.down(992)]: {
      padding: "16px",
    },
  },
  close: {
    position: "absolute",
    top: 0,
    right: 0,
  },
  title: {
    textAlign: "center",
    color: "#000",
    fontWeight: 500,
    letterSpacing: "-0.04em",
    lineHeight: "56px",
    marginBottom: 24,
    [theme.breakpoints.down(992)]: {
      fontSize: 48,
    },
    [theme.breakpoints.down(768)]: {
      fontSize: 34,
      lineHeight: "40px",
    },
  },
  subtitle: {
    color: "#64748B",
    marginLeft: 10,
  },
}));
