/* eslint-disable eqeqeq */
import React, { useEffect, useState } from "react";
import {
  makeStyles,
  Select,
  Button,
  FormControl,
  MenuItem,
  TextField,
  IconButton,
  ClickAwayListener,
  Tooltip,
} from "@material-ui/core";
import { Container, Typography } from "@mui/material";
import { Box } from "@mui/system";
import { useDispatch, useSelector } from "react-redux";
import CompareArrowsIcon from "@mui/icons-material/CompareArrows";
import { useFormik } from "formik";
import { CreateBuy } from "../../../redux/reducers/buy";
import { CreateSell } from "../../../redux/reducers/sell";
import { QuestionIcon } from "../../../assets/images/icons";
import SnackBar from "../../../components/SnackBar";

const Exchange = ({ t }) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const [open, setOpen] = useState(false);
  const [response, setResponse] = useState({});

  const { СurrenciesList } = useSelector(
    ({ СurrenciesReducer }) => СurrenciesReducer
  );

  const { answerCreateBuy } = useSelector(({ BuyReducer }) => BuyReducer);
  const { answerCreateSell } = useSelector(({ SellReducer }) => SellReducer);

  const formik = useFormik({
    initialValues: {
      currencyToSell: {
        id: "2",
      },
      currencyToBuy: {
        id: "1",
      },
      sumToSell: 0,
      sumToBuy: 0,
    },
    onSubmit: (values) => {
      if (values.currencyToSell?.id == 1) {
        dispatch(
          CreateBuy({
            sum: values.sumToBuy,
            currency: values.currencyToBuy.currency,
          })
        );
      } else {
        dispatch(
          CreateSell({
            sum: values.sumToBuy,
            currency: values.currencyToSell.currency,
          })
        );
      }
    },
  });

  const values = formik.values;

  useEffect(() => {
    const usdt = СurrenciesList?.find((item) => item?.id == 1);
    formik.setFieldValue("currencyToSell", usdt);
    const btc = СurrenciesList?.find((item) => item?.id == 2);
    formik.setFieldValue("currencyToBuy", btc);
    // eslint-disable-next-line
  }, [СurrenciesList]);

  useEffect(() => {
    if (values.currencyToSell?.id == 1) {
      formik.setFieldValue(
        "sumToSell",
        (values.sumToBuy / values.currencyToBuy?.rate).toFixed(
          values.currencyToBuy?.decimal
        )
      );
    } else {
      formik.setFieldValue(
        "sumToSell",
        (values.sumToBuy * values.currencyToSell?.rate).toFixed(
          values.currencyToSell?.decimal
        )
      );
    }
    // eslint-disable-next-line
  }, [values.sumToBuy, values.currencyToBuy, values.currencyToSell]);

  useEffect(() => {
    if (values.currencyToSell?.id == 1) setResponse(answerCreateBuy);
    else setResponse(answerCreateSell);
    // eslint-disable-next-line
  }, [answerCreateBuy, answerCreateSell]);

  return (
    <>
      <SnackBar state={response} setState={setResponse} />
      <Box
        component="section"
        sx={{
          color: "#000",
        }}
      >
        <Container
          maxWidth="lg"
          sx={{
            height: "50vh",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            flexDirection: "column",
          }}
        >
          <Typography variant="h3" mb={3} fontWeight="500" textAlign="center">
            {t("quickExchange")}
          </Typography>
          <Box
            component="form"
            width={{ xs: "100%", lg: "70%" }}
            onSubmit={formik.handleSubmit}
          >
            <Box display="flex" columnGap="16px" alignItems="center">
              <FormControl fullWidth className={classes.formControl}>
                <Select
                  fullWidth
                  value={values.currencyToSell}
                  name="currencyToSell"
                  onChange={formik.handleChange}
                  variant="outlined"
                >
                  {Array.isArray(СurrenciesList) &&
                    СurrenciesList
                      .filter((item) =>
                        values.currencyToSell?.id != 1
                          ? item.currency !== "USDT"
                          : item.currency === "USDT"
                      )
                      .map((item, idx) => (
                        <MenuItem key={idx} value={item}>
                          <img src={item.logo} className={classes.img} alt="" />
                          {item.currency}
                        </MenuItem>
                      ))}
                </Select>
              </FormControl>
              <IconButton
                onClick={() => {
                  formik.setFieldValue("currencyToBuy", values.currencyToSell);
                  formik.setFieldValue("currencyToSell", values.currencyToBuy);
                }}
              >
                <CompareArrowsIcon sx={{ fontSize: 40 }} />
              </IconButton>
              <FormControl fullWidth className={classes.formControl}>
                <Select
                  fullWidth
                  value={values.currencyToBuy}
                  name="currencyToBuy"
                  onChange={formik.handleChange}
                  variant="outlined"
                >
                  {Array.isArray(СurrenciesList) &&
                    СurrenciesList
                      .filter((item) =>
                        values.currencyToBuy?.id != 1
                          ? item.currency !== "USDT"
                          : item.currency === "USDT"
                      )
                      .map((item, idx) => (
                        <MenuItem key={idx} value={item}>
                          <img src={item.logo} className={classes.img} alt="" />
                          {item.currency}
                        </MenuItem>
                      ))}
                </Select>
              </FormControl>
            </Box>
            <Box display="flex" columnGap="16px" alignItems="center">
              <TextField
                name="sumToBuy"
                value={values.sumToBuy}
                onChange={formik.handleChange}
                fullWidth
                label={t("IGive")}
                // InputProps={{
                //   endAdornment: (
                //     <InputAdornment
                //       position="end"
                //       className={classes.InputAdornment}
                //     >
                //       Максимум <div className="primary">0</div>
                //     </InputAdornment>
                //   ),
                // }}
                variant="outlined"
                className={classes.formControl}
              />
              <TextField
                name="sumToSell"
                value={values.sumToSell === "NaN" ? "0" : values.sumToSell}
                onChange={formik.handleChange}
                fullWidth
                label={t("IGet")}
                variant="outlined"
                className={classes.formControl}
              />
            </Box>
            <Box
              display="flex"
              width={{ xs: "100%", lg: "70%" }}
              justifyContent="space-between"
            >
              <div>
                <Typography variant="body2" fontWeight="600">
                  {t("currentRate")}
                </Typography>
                <Typography variant="body1">
                  1 {values?.currencyToSell?.currency} ≈{" "}
                  {values.currencyToSell?.id != 1
                    ? values?.currencyToSell?.rate
                    : values?.currencyToBuy?.rate}{" "}
                  {values?.currencyToBuy?.currency}
                </Typography>
              </div>
              <Box display="flex" alignItems="start">
                <div>
                  <Typography variant="body2" fontWeight="600">
                    {t("rateFor24")}
                  </Typography>
                  <Typography
                    variant="body1"
                    sx={{
                      color:
                        values?.currencyToBuy?.difference &&
                        values?.currencyToBuy?.difference[0] === "+"
                          ? "#10B981"
                          : values?.currencyToBuy?.difference &&
                            values?.currencyToBuy?.difference[0] === "-"
                          ? "#EF4444"
                          : "#A0ABBB",
                    }}
                  >
                    {values?.currencyToBuy?.difference === undefined
                      ? "0.00"
                      : values?.currencyToBuy?.difference}
                  </Typography>
                </div>
                <ClickAwayListener onClickAway={() => setOpen(false)}>
                  <Tooltip
                    PopperProps={{
                      disablePortal: true,
                    }}
                    title={t("warning")}
                    placement="top-start"
                    onClose={() => setOpen(false)}
                    open={open}
                    disableFocusListener
                    disableHoverListener
                    disableTouchListener
                  >
                    <Button
                      className={classes.question}
                      onClick={() => setOpen(true)}
                    >
                      <QuestionIcon />
                    </Button>
                  </Tooltip>
                </ClickAwayListener>
              </Box>
            </Box>
            <Button
              variant="contained"
              fullWidth
              type="submit"
              sx={{ mt: 2 }}
              className={classes.button}
              size="large"
              color="primary"
            >
              {t("exchange")}
            </Button>
          </Box>
        </Container>
      </Box>
    </>
  );
};

export default Exchange;

const useStyles = makeStyles((theme) => ({
  button: {
    height: 56,
    width: "100%",
    display: "block",
    marginTop: 16,
  },

  formControlFirst: {
    boxShadow: "none",
    "& .MuiInputLabel-formControl": {
      left: 14,
    },

    "& .MuiInputLabel-shrink": {
      left: 0,
    },
  },

  formControl: {
    backgroundColor: "#FFF",
    marginBottom: 16,

    "& .MuiInputLabel-formControl": {
      left: 14,
    },

    "& .MuiInputLabel-shrink": {
      left: 0,
    },
  },

  img: {
    width: 30,
    height: 30,
    marginRight: 10,
    [theme.breakpoints.down(768)]: {
      display: "none",
    },
  },

  // InputAdornment: {
  //   display: "flex",
  //   flexDirection: "column",
  // },

  calculator: {
    marginTop: 20,
  },
  icon: {
    [theme.breakpoints.down(768)]: {
      transform: "rotate(90deg)",
    },
  },
}));
